import React from 'react';
import ReactDOM from 'react-dom';

// imports from react-redux and redux
import { Provider } from 'react-redux';
import { createStore, compose, applyMiddleware } from 'redux';
import logger from 'redux-logger'; //permite monitorear el store por la consola
import reducer from './reducers';

import "core-js/stable";
import "regenerator-runtime";

import App from './routes/App';

const initialState = {
    favoriteCharacters: [],
    sectionActive: 'Characters'
};

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
// implementamos una constante que contiene una difinicin de composicin, la cual contiene primero
// compose -> definicin que venga de la gestin del logger o el store
// window__REDUX_DEVT... -> trabaja con la definicin de redux devtools, la cual se ejecuta como una ventana

const store = createStore(reducer, initialState, composeEnhancers(applyMiddleware(logger)));
//createStore() siempre recibe dos parmetros
//reducer:  encargado de recibir todo tipo de acciones (desde el Action Store) que me permitirn modificar el store
//  composeEnhancers(applyMiddle...(logger)) -> interceptor de cara a la creacin del store que trabajar con el logger
ReactDOM.render( < Provider store = { store } > < App /> </Provider>,document.getElementById('root'));