//payload: data que devolver cuando se ejecute una accin
export const setFavorite = (payload) => ({
    type: 'SET_FAVORITE',
    payload
});

export const deleteFavorite = (payload) => ({
    type: 'DELETE_FAVORITE',
    payload
});

export const setSection = (payload) => ({
    type: 'SET_SECTION',
    payload
})